.displayContainer {
	position: absolute;
	top: 0;
	left: 0;
	width: 100vw;
	height: 100vh;
}

.image {
	position: absolute;
  top: 0; right: 0; bottom: 0; left: 0;
	display: block;
	margin: auto;
	text-align: center;
	border: 0px;
	max-width: 100%;
	max-height: 100%;
}

.text {
  margin: auto;
	font-size: 30px;
	color: #dddddd;
	text-shadow: 0 0 4px #000000;
  top:0;
  bottom:0;
	text-align: center;
	max-width: 100%;
	max-height: 100%;
}